<template>
	<div class="container">
		<div class="history-header">
			<div class="history-block">
				<h2 class="history-title">История изменений</h2>
			    <router-link to="/" class="web-link web-link--short history-link">На главную</router-link>
			</div>
			<button class="btn btn-reset" @click="resetHistory">Очистить историю</button>
		</div>
		<div class="history-container">
			<table class="table">
				<tr>
					<th>Дата</th>
					<th>Время</th>
					<th>Событие</th>
					<th>Фильм</th>
				</tr>
				<tr v-for="elem in wholeHistory" :class="{
					red: elem.type === 'removed',
					green: elem.type === 'added'
				}">
					<td>{{elem.date.split(',')[0]}}</td>
					<td>{{elem.date.split(',')[1]}}</td>
					<td v-show="elem.type === 'added'">Добавлено в избранные</td>
					<td v-show="elem.type === 'removed'">Удалено из избранных</td>
					<td>{{elem.name}}</td>
				</tr>
			</table>
		</div>
	</div>
</template>

<script>

	import {mapGetters, mapActions} from 'vuex';
	
	export default {
		name: 'history',
		computed: {
			...mapGetters('history', { wholeHistory: 'wholeHistory'} ),
		},
		methods: {
			...mapActions('history', { resetHistory: 'resetHistory'}),
		}
	};

</script>

<style>
	.table {
		width: 100%;
		margin-bottom: 20px;
		border: 1px solid #020202;
		border-collapse: collapse; 
	}
	.table th {
		font-weight: bold;
		padding: 5px;
		color: #1f4256;
	}
	.table td {
		border: 1px solid #20273a;
		padding: 5px;
		text-align: center;
		font-weight: bold;
	}

	.red {
		background: #2d3447;
		color: #cc5d6d;
	}

	.green {
		background: #313c4e;
		color:  #fff;
	}

	@media (max-width:  700px) {
		.table th:nth-child(2) {
			display: none;
		}

		.table td:nth-child(2) {
			display: none;
		}
	}

	@media (max-width:  420px) {
		.table th:nth-child(1) {
			display: none;
		}

		.table td:nth-child(1) {
			display: none;
		}
	}


</style>